import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

import MyProjects from "../components/myProjects/myProjects"
import Buttons from "../components/stylesComponents/buttons"
import Container from "../components/stylesComponents/container"
import { Arrow } from "../components/arrow.js"

class SecondPage extends React.Component {
  componentDidMount() {
    window.addEventListener("scroll", this.trackScrolling)
    document.getElementById("arrow").style.opacity = 1
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.trackScrolling)
  }

  isBottom(el) {
    return el.getBoundingClientRect().bottom <= window.innerHeight + 50
  }

  isArrow = true

  trackScrolling = () => {
    let wrappedElement = document.getElementById("footer")
    if (this.isBottom(wrappedElement) && this.isArrow) {
      this.isArrow = false
      document.getElementById("arrow").style.opacity = 0
    }
    if (!this.isBottom(wrappedElement) && !this.isArrow) {
      this.isArrow = true
      document.getElementById("arrow").style.opacity = 1
    }
  }

  render() {
    return (
      <Layout>
        <SEO title="My Projects" />
        <Container>
          <Arrow id="arrow" />
          <Buttons
            firstName="home"
            secondName="about me"
            title="My projects"
            to="/aboutMe/"
          />
          <MyProjects />
          <span style={{ width: 1, height: 1 }} id="footer" />
        </Container>
      </Layout>
    )
  }
}

export default SecondPage
